.container {
  margin-left: 5vw;
  margin-right: 5vw;
  margin-top: 2vh;

  display: flex;
  flex-direction: column;
  justify-content: center;
  align-content: center;
  align-items: center;
}

.pagination {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  margin-top: 2vh;
  margin-bottom: 5vh;
  gap: 50px;
}

.button {
  cursor: pointer;
}
